@import "../../../general/size";

.professor{
    display:flex;
    flex-direction: column;
   // background-color: bisque;
    margin-right: 250px;
    width:100%;
}

.prof-header{
    
    position: sticky;
    top:150px;    
    z-index: 1; 
    display: flex;
    font-size: 26px;
    flex-direction: column;
    font-weight:700;
    background-color: white;
    width:100%;
    
}
.header-title{
    width:100%;
    display: flex;
  
    margin-left:50px;
    margin-bottom: 23.5px;

    font-family: Roboto;
    
    }

.prof-scrollMenu{
    
  
    display: flex;
    width: 100%;
   // padding: 10px;
    justify-content: space-around;
    border-style: solid;
    border-top-width:1px;
    border-left-width:0px;
    border-right-width:0px;
    border-bottom-width:1px;
    border-color: #707070;
    font-family: 'Roboto' ;
   background-color: white;
   //background-color: red;
}
.profList{
    
    font-size: 22px;
    list-style: none;
    display:flex;
    width: 100%;
    justify-content: space-around;
    font-family: Roboto ;
    margin-top: 10px;
    margin-bottom: 10px;
    cursor:pointer;
    font-weight: 1000;
  //background-color: red;
    
}
.profListItem{
    //background-color: red;
    font-family: Roboto ;
    display: flex;
}

.prof-profile{
    width: 100%;
    margin-top: 20px;
    margin-bottom: 50px;
    //margin-left:91px;
    display: flex;
    
    
    justify-content: center;
    
   // background-color: red;
}

.prof-summary{
    display: flex;
    flex-direction: column;
  // background-color: red;
    margin-left: 37px;
    
    
   // height:301px;
    
    width:100%;
    
}
.research-interest{
    display:flex;
    flex-direction: column;
   // background-color: blue;
    
}
.prof-photo{
    display: flex;
    margin-left: 40px;
   // width:100%;
}
.profImg{
    margin-top:20px;
    display: flex;
    justify-self: center;
    width:212px;
    height: 276px;
    //height:350px;
}

.prof-name{
    
    font-size: 26px;
   // font-family: 'Segoe UI';
